import { Component, OnInit } from '@angular/core';


import { HttpClient } from '@angular/common/http';
import { IMovie, Movie} from '../movies/movies';
import { MovieService } from '../movies/movies.service';
import { CompileShallowModuleMetadata } from '@angular/compiler';


@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css']
})
export class AdminPanelComponent implements OnInit {
  ADD_url = 'http://localhost:8080/movie/add';
  GETALL_url = 'http://localhost:8080/movie/getAll';
  DEL_url = 'http://localhost:8080/movie/delete/';
  MOD_url = 'http://localhost:8080/movie/update';

  movies: IMovie[];
  movie: Movie=new Movie();

  display: boolean = false;
  loaded: boolean = false;
  expanded: boolean = false;
  expandedId: number=0;

  selected: any;
  editing: boolean = false;

  displaingElement: boolean = false;
  addingElement: boolean = false;
  removingElement: boolean = false;

  constructor(
    private http: HttpClient,
    private movieService: MovieService,
  ){}

  displayDisplaingElement(){this.displaingElement=!this.displaingElement;}
  displayAddingElement(){this.addingElement=!this.addingElement;}
  displayRemovingElement(){this.removingElement=!this.removingElement;}

  async submit(movie){
    this.http.post(this.ADD_url, movie).toPromise()
    .then((data:any) => { 
      console.log(data);
      console.log(JSON.stringify(data.json));
    });
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }
  
  id: number;

  async delete(id){
    this.http.delete(this.DEL_url+id).toPromise()
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

   async deleteRow(id){
    this.http.delete(this.DEL_url+id).toPromise()
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

  async modify(movie){
    console.log(JSON.stringify(movie));
    this.http.put(this.MOD_url, movie).toPromise()
    .then((data:any) => { 
      console.log(data);
      console.log(JSON.stringify(data.json));
    });
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

   edit(){
    this.editing = true;
   }

  expandRow(id){
    if(this.expandedId==0){
      this.expandedId = id;
      this.expanded = !this.expanded;
      return;
    } 
    if(this.expandedId==id){
      this.expanded = !this.expanded;
      this.expandedId = 0;
      return;
    }
    this.expandedId = id;
  }

  onDisplay(){
    this.display=!this.display;
  }

  onLoad(){
    this.movieService.getMovies().subscribe({
      next: movies =>{ 
        this.movies=movies;
      },
      error: err =>this.errorMessage = err
    });
    this.loaded=true;
  }
  

  private errorMessage="/movie/getAll is unavailable"


  ngOnInit(): void {

  }

  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }


}


